let currentPageName = "create-by-hand-dialog";

export default {
  name: currentPageName,
  data() {
    return {
      formLabelWidth: '120',
      itemNewStatus: '',
      selectStatusList: [
        {
          name: '未确定',
          value: 0
        }, {
          name: '已确定',
          value: 1
        }, {
          name: '待执行',
          value: 2
        }, {
          name: '已执行',
          value: 3
        }

      ],
      groupsValue: [
      ]
    }
  },
  props: {
    showDialog: {
      type: Boolean,
      default: false
    },
    dialogData: {
      type: Object,
      default: function () {
        return {}
      }
    },
    btnCancel: {
      type: Function,
      default: function () {
      }
    },
    btnOk: {
      type: Function,
      default: function () {
      }
    },
    selectGroupList: {
      type: Array,
      default: function () {
        return []
      }
    },
    recommandSelectGroupList: {
      type: Array,
      default: function () {
        return []
      }
    }

  },
  computed: {
    /**
    * 过滤按一定顺序的选择数组
    *
    * @returns
    */
    getWholeSelecGroupList: function () {
      let that = this
      let tempArray = []
      let { selectGroupList,
        recommandSelectGroupList } = that

      if (selectGroupList ===
        recommandSelectGroupList) {
        return selectGroupList
      }

      let unRecommandList = selectGroupList.filter((value) => {
        for(let recommandValue of recommandSelectGroupList){
          if(recommandValue.userName===value.userName){
            return false
          }
        }
        return  true
      })

      let recommandList = recommandSelectGroupList.map((value) => {
        return Object.assign({}, value, { recomamdText: '推荐' })
      })

      alert

      tempArray = recommandList.concat(unRecommandList)

      return tempArray
    },
    /**
  * 转换日状态
  *
  * @returns
  */
    getCourseDay: function () {
      let that = this
      let result = ''
      if (that.dialogData.course && that.dialogData.course.day) {

        let { day } = that.dialogData.course
        switch (day) {
          case 1:
            result = '星期一'
            break
          case 2:
            result = '星期二'
            break
          case 3:
            result = '星期三'
            break
          case 4:
            result = '星期四'
            break
          case 5:
            result = '星期五'
            break
          case 6:
            result = '星期六'
            break
          case 7:
            result = '星期天'
            break
          default:
            break;
        }
      }
      return `(${result})`
    },
    /**
     * 转换状态
     *
     * @returns
     */
    getCurrentItemStatus: function () {
      let { dialogData } = this
      if (dialogData.status) {
        if (status === 0) {
          return '未确定'
        } else if (status === 1) {
          return '已确定'
        } else if (status === 2) {
          return '待执行'
        } else if (status === 3) {
          return '已执行'
        }
      } else {
        return '未确定'
      }
    },

    /**
     * 过滤与当前item相同的状态
     *
     * @returns
     */
    getFillterItemStatusList: function () {
      let { selectStatusList, dialogData } = this
      return selectStatusList.filter((value) => {
        return value.value !== dialogData.status
      })
    },
  },
  watch: {
    showDialog: function (value) {
      let that = this
      if (value) {
        that.dialogData.groups ? that.groupsValue = that.dialogData.groups.split(',') : null
      }
    }

  },
  methods: {

  }

}




