let currentPageName = "course-arrage-table";

export default {
  name: currentPageName,
  data() {
    return {
      searchTeacherInput: '',
      selectedWeek: '',
      selectedDay: '',
      selectedStatus: '',
      selectedDate:'',
      setlectedItemsList: [],
      selectDayList: [{
        name: '星期一',
        value: 1
      }, {
        name: '星期二',
        value: 2
      }, {
        name: '星期三',
        value: 3
      }, {
        name: '星期四',
        value: 4
      }, {
        name: '星期五',
        value: 5
      }, {
        name: '星期六',
        value: 6
      }, {
        name: '星期天',
        value: 7
      }],
      btnDeletedDisable: true,
      btnSearchDisable: true,
      constValue: false
    }
  },
  props: {
    selectStatusList: {
      type: Array,
      default: function () {
        return []
      }
    },
    btnSearchClick: {
      type: Function,
      default: function () {
      }
    },
    btnResetClick: {
      type: Function,
      default: function () {
      }
    },
    btnDeleteClick: {
      type: Function,
      default: function () {
      }
    },
    btnAutoCreateClick: {
      type: Function,
      default: function () {
      }
    },
    btnExportClick:{
      type:Function,
      default:function(){

      }
    },
    btnItemDeleteClick: {
      type: Function,
      default: function () {
      }
    },
    btnItemChangeStatusClick: {
      type: Function,
      default: function () {
      }
    },
    btnItemCreateByHandClick: {
      type: Function,
      default: function () {
      }
    },
    btnItemDownloadClick:{
      type:Function,
      default:function(){
      }
    },
    tableDataResource: {
      type: Array,
      default: function () {
        return []
      }
    },
    currentPage: {
      type: Number,
      default:1
    },
    currentChange: {
      type: Function,
      default: function () {
      }
    },
    total: {
      type: Number,
      default: 0
    },
    showChangeBtn: {
      type: Boolean,
      default: false
    },
    showAutoCreateBtn: {
      type: Boolean,
      default: false
    },
    showMutiplyDeleteBtn: {
      type: Boolean,
      default: true
    },
    showWeekSelected:{
      type:Boolean,
      default:true
    },
    showDaySelected:{
      type:Boolean,
      default:true
    },
    showDateSelected:{
      type:Boolean,
      default:false
    },
    showItemDeleteBtn: {
      type: Boolean,
      default: true
    },
    showCourseContent:{
      type:Boolean,
      default:true
    },
    showStatusSelection: {
      type: Boolean,
      default: false
    },
    showCreateByHand: {
      type: Boolean,
      default: false
    },
    isResetAll: {
      type: Boolean,
      default: false
    },
    showArragePeopleCount: {
      type: Boolean,
      default: false
    },
    showArragePeople: {
      type: Boolean,
      default: true
    },
    showExportExcelBtn:{
      type:Boolean,
      default:false
    },
    showItemDownloadBtn:{
      type:Boolean,
      default:false
    }


  },
  computed:{
    getBtnSearchDisabled:function(){
      let {
        selectedWeek,
        searchTeacherInput,
        selectedDay,
        selectedStatus,
        selectedDate
      }=this
      return !(selectedWeek>0||selectedDay>0||searchTeacherInput.trim().length>0||selectedStatus>0||selectedStatus===0||selectedDate.length>0)
    }
  },
  watch: {
    isResetAll: function (value) {
      let that = this
      if (value !== that.constValue) {
        this.resetLocalData()
        that.constValue = value
      }
    },
    setlectedItemsList: function (value) {
      if (value.length > 0) {
        this.btnDeletedDisable = false
      } else {
        this.btnDeletedDisable = true
      }
    },
    selectedWeek: function (value) {
      let that = this
      if (value > 0 || that.searchTeacherInput.trim().length > 0 || that.selectedDay > 0 || that.selectedStatus > -1) {
        that.btnSearchDisable = false
      } else {
        that.btnSearchDisable = true
      }
    },

    searchTeacherInput: function (value) {
      let that = this
      if (value.trim().length > 0 || that.selectedWeek > 0 || that.selectedDay > 0 || that.selectedStatus > -1) {
        that.btnSearchDisable = false
      } else {
        that.btnSearchDisable = true
      }
    },
    selectedDay: function (value) {
      let that = this
      if (value > 0 || that.searchTeacherInput.trim().length > 0 || that.selectedWeek > 0 || that.selectedStatus > -1) {
        that.btnSearchDisable = false
      } else {
        that.btnSearchDisable = true
      }
    },
    selectedStatus: function (value) {
      let that = this
      if (value > -1 || that.searchTeacherInput.trim().length > 0 || that.selectedWeek > 0 || that.selectedDay > 0) {
        that.btnSearchDisable = false
      } else {
        that.btnSearchDisable = true
      }
    },


  },
  methods: {
    getItemArrargePeopleLength: function (item) {
      if (item.groups) {
        return item.groups.split(',').length
      }
      return '-'
    },
    clickReset: function () {
      let that = this
      that.resetLocalData()
      that.btnResetClick ? that.btnResetClick() : null

    },
    resetLocalData: function () {
      let that = this
      that.searchTeacherInput = ''
      that.selectedWeek = ''
      that.selectedDay = ''
      that.selectedStatus = ''
      that.selectedDate=''
      that.setlectedItemsList = []
      setTimeout(() => {
        that.btnSearchDisable = true
      }, 100);
      that.btnDeletedDisable = true
      that
        .$refs
        .comfirmTable
        .clearSelection()
      that.replaceRouter()
    },
    clickSearch: function () {
      let that = this
      that.replaceRouter()
    },
    clickCurrentChange: function (page) {
      let that = this
      this.currentChange ? this.currentChange({ page, ...that.$route.query }) : null
    },
    selectionChange: function (value) {
      this.setlectedItemsList = value
    },
    replaceRouter: function () {
      let that = this
      that.$router.replace({
        name: that.$route.name,
        query: {
          ...that.$route.query,
          searchTeacherInput: that.searchTeacherInput,
          selectedWeek: that.selectedWeek,
          selectedDay: that.selectedDay,
          selectedStatus: that.selectedStatus,
          selectedDate:that.selectedDate
        }
      })

    }
  }

}




